/* Dark mode (DEFAULT(Cause its better))*/
:root {
	--blue: #334367;
	--darkBlue: #293651;
	--red: #b42d58;
	--darkRed: #832b47;
	--lightBackground: #cbbfbb;
	--background: #1c1e24;
	--darkBackground: #16161b;
	--text: #fffdf7;
	--darkText: #17161b;
	--blackImgFilter: invert(97%) sepia(97%) saturate(12%) hue-rotate(197deg)
		brightness(105%) contrast(104%);
	--whiteImgFilter: ;
}
/* Light mode */
[data-theme="light"] {
	--blue: #284b63;
	--darkBlue: #0b4f6c;
	--red: #854b4c;
	--darkRed: #8d4043;
	--lightBackground: #c4e9d0;
	--background: #d7d1cf;
	--darkBackground: #353535;
	--text: #000000;
	--darkText: #ffffff;
	--blackImgFilter: ;
	--whiteImgFilter: invert(97%) sepia(97%) saturate(12%) hue-rotate(197deg)
		brightness(105%) contrast(104%);
}

html {
	font-size: calc(15px + 0.5vw);
}

body {
	font-family: "Montserrat", sans-serif;
	background-color: var(--background);
}

a {
	color: var(--text);
	text-decoration: none;
}

a:hover {
	color: var(--red);
}

.bigMe {
	align-self: center;
	width: 35%;
	max-width: 300px;
	height: auto;
	border-radius: 50%;
	padding: 1.5rem;
}

.hero {
	display: flex;
	flex-direction: row;
	width: 100%;
	flex-wrap: wrap;
	justify-content: stretch;
	align-items: stretch;
}
.heroLeft {
	flex-grow: 1;
	min-width: 600px;
	width: 60vw;
	height: auto;
	background-color: var(--blue);
	color: white;
	display: flex;
	justify-content: space-evenly;
	flex-direction: row;
}
.heroRight {
	flex-grow: 1;
	min-width: min-content;
	width: 35vw;
	height: auto;
	background-color: var(--red);
	color: white;
}
.heroRight li {
	padding: 0.5rem 0;
}
@media screen and (max-width: 600px) {
	.heroLeft {
		text-align: center;
		flex-wrap: wrap;
		min-width: 0;
	}
	.bigMe {
		width: 40%;
	}
	.heroRight {
		text-align: center;
	}
}

.heroText {
	font-size: 1rem;
	align-self: center;
	padding: 0.5rem 1rem;
}

.nav {
	font-family: "Sono", sans-serif;
	background-color: var(--darkbackground);
	height: auto;
	padding: 1rem;
	display: flex;
	justify-content: space-between;
	align-items: center;
	font-size: 0.5rem + 1vw;
	flex-wrap: wrap;
}
/*justify center if mobile*/
@media only screen and (max-width: 450px) {
	.nav {
		justify-content: center;
	}
}
.navLinks {
	flex-wrap: nowrap;
	flex-shrink: 1;
}

button {
	background-color: transparent;
	border: none;
	color: var(--darkBlue);
	font-size: 2rem;
	padding: 0;
	margin: 0;
}

.navLogo {
	height: 2rem;
	width: auto;
	/* if darkmode invert colors */
	filter: var(--blackImgFilter);
}

.navButtons {
	padding-top: 0.5rem;
}

.navLogo:hover {
	/* for --red */
	filter: invert(28%) sepia(78%) saturate(1346%) hue-rotate(309deg)
		brightness(93%) contrast(95%);
	/* for --blue */
	/* filter: invert(30%) sepia(53%) saturate(509%) hue-rotate(182deg)
		brightness(84%) contrast(84%); */
	cursor: pointer;
}

.navLinks a {
	padding: 0 2vw;
}

.navButtons img {
	padding: 0 2vw;
}
